angular.module('app.directives')
    .directive('projectFileDownload',
    ['$timeout','$window','appConfig', 'ProjectFile',  function($timeout,$window,appConfig,ProjectFile){
        return {
            restrict: 'E',
            templateUrl: appConfig.baseUrl + '/build/views/templates/projectFileDownload.html',
            link: function(scope, element, attr){
                var anchor = element.children()[0];
                /*Evento*/
                scope.$on('salvar-arquivo',function(event,data){

                     $(anchor).removeClass('disabled');
                     $(anchor).text('Save File');
                     blobUtil.base64StringToBlob(data.file).then(function (blob) {
                        /*Indica que temos um dado binÃ¡rio no href e indica que estÃ¡ com o base64*/
                         $(anchor).attr({
                            href: $window.URL.createObjectURL(blob,data.mime_type),
                            download: data.name
                         });

                     });


                     /*TimeOut para dar um leg de segundo para nÃ£o apresentar erro*/
                     $timeout(function(){

                        /* apÃ³s executar a primeira vez, irÃ¡ limpar o $scope.downloadFile para
                            nÃ£o ficar num loop */
                        scope.downloadFile = function (){
                            $(anchor).text('Download');
                        };
                        /*irÃ¡ pegar a ancora e chamar o metodo de click para clicar em download e jÃ¡ baixar o arquivo*/
                        $(anchor)[0].click();
                     });
                });
            },
            /*
            $element = ServiÃ§o que permite ter acesso na diretiva
            var anchor = $element.children()[0]; = pega o elemento da ancora, como
                                                   temos somente 1 elemento entÃ£o colocamos 0, pois ele Ã©
                                                   o elemento zero
            */
            controller: ['$scope','$element','$attrs',
                function($scope,$element,$attrs){
                    /*
                      desabilita o botÃ£o para o download do arquivo
                    */
                    $scope.downloadFile = function (){
                        var anchor = $element.children()[0];
                        $(anchor).addClass('disabled');
                        $(anchor).text('Loading...');

                        ProjectFile.download({id: $attrs.idProject, idFile: $attrs.idFile}, function(data){
                            $scope.$emit('salvar-arquivo',data);
                        });
                    }
            }]
        };
    }
]);